import { FunctionComponent, useCallback, useMemo } from "react";
import styled from "styled-components";
import { OptionState } from "../context/Filters.context";
import { Checkbox } from './Checkbox';
import { MultiSelectDropdown } from './MultiSelectDropdown';

const TaxonomyTitle = styled.legend`
    margin: 0 0 .5rem;
    font-size: var(--body-text-size, .875rem);
    font-weight: 600;
`;

const TaxonomyWrap = styled.fieldset`
    margin: 0 0 2rem;
    border: none;
    padding: 0;
`;

interface ComponentProps {
    title: string;
    options: { [option: string]: OptionState },
    selectOption: (optionId: string) => void;
    clearAll: () => void;
}

export const TaxonomyOptionSelector: FunctionComponent<ComponentProps> = ({ title, options, selectOption, clearAll }) => {

    const handleChange = useCallback((option: string, disabled = false): void => {
        if (disabled) {
            return;
        }
        selectOption(option);
    }, [selectOption]);

    const useDropdown = useMemo(() => Object.keys(options).length > 3, [options]);

    return <TaxonomyWrap>
        {useDropdown ?
            <MultiSelectDropdown
                label={title}
                clearAll={clearAll}
                selectOption={handleChange}
                options={Object.keys(options).map((option) => options[option])}
            >
                <TaxonomyTitle>{title}</TaxonomyTitle>
            </MultiSelectDropdown>
            :
            <>
                <TaxonomyTitle>{title}</TaxonomyTitle>
                {Object.keys(options).map((key) => {
                    const option = options[key];
                    return <Checkbox
                        key={key}
                        name={option.id}
                        checked={option.selected}
                        id={option.id}
                        label={option.title}
                        disabled={option.disabled}
                        onChange={() => handleChange(option.id, option.disabled)}
                    />;
                })}
            </>
        }
    </TaxonomyWrap>;
}