import Icon from "carbon-react/lib/components/icon";
import Typography from "carbon-react/lib/components/typography";
import { FunctionComponent, useCallback, useContext, useEffect, useReducer, useRef } from "react";
import styled from "styled-components";
import { useLabels } from "../context/Labels.context";
import { SidebarContext } from "../context/Sidebar.context";
import { TaxonomySelector } from './TaxonomySelector';

const BackgroundEle = styled.div`
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: rgba(0, 20, 29, 0.6);
    z-index: 1000001;
`;

const SidebarEle = styled.div`
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    width: 320px;
    z-index: 1000002;
    background: rgb(242 244 245);
    box-shadow: rgb(0 20 29 / 10%) 0px 10px 30px 0px, rgb(0 20 29 / 10%) 0px 30px 60px 0px;
    overflow-y: auto;
`;

const SidebarTitle = styled.div`
    background: #fff;
    padding: 1.5rem 2rem;
    display: flex;
    justify-content: space-between;
`;

const SidebarContent = styled.div`
    padding: 2rem;
    border-bottom: 1px solid rgba(0,0,0,0.1);
`;

const CloseButton = styled.button`
    padding: 0;
    width: 1.5rem;
    height: 1.5rem;
    border: none;
    background: none;
    cursor: pointer;
    &:focus{
        outline: rgb(255, 181, 0) solid 3px
    }
`;

interface State {
    styles: {
        bg: {
            opacity: number
        },
        sidebar: {
            transform: string,
            opacity: number
        },
    }
    animating: boolean,
    direction: 'OPENING' | 'CLOSING'
}

const animationReducer = (state: State, action: any): State => {
    switch (action.type) {
        case "OPEN":
            return {
                ...state,
                animating: state.direction === 'CLOSING',
                direction: 'OPENING',
            };
        case "CLOSE":
            return {
                ...state,
                animating: state.direction === 'OPENING',
                direction: 'CLOSING',
            };
        case "DONE":
            return {
                ...state,
                animating: false
            }
        case "TICK":
            const t = action.payload;
            let styles;
            if (state.direction === 'OPENING') {
                const sidebarT = Math.max(0, Math.min((t - .4) / .6, 1));
                styles = {
                    bg: {
                        opacity: Math.min(t / .5, 1),
                    },
                    sidebar: {
                        opacity: sidebarT,
                        transform: `translate(${(1 - sidebarT) * 100}%)`
                    }
                }
            } else {
                const sidebarT = Math.max(0, Math.min((t) / .6, 1));
                styles = {
                    bg: {
                        opacity: 1 - ((t - .5) / .5),
                    },
                    sidebar: {
                        opacity: 1 - sidebarT,
                        transform: `translate(${sidebarT * 100}%)`
                    }
                }
            }
            return {
                ...state,
                styles
            };
        default:
            return state;
    }
}

const useAnimation = (duration = 500) => {
    const [animation, dispatch] = useReducer(animationReducer, {
        animating: false,
        direction: 'CLOSING',
        styles: {
            bg: {
                opacity: 0,
            },
            sidebar: {
                opacity: 0,
                transform: 'translateX(-100%)',
            }
        }
    });

    useEffect(() => {
        if (!animation.animating) {
            return;
        }

        let done = false;
        const start = Date.now();

        const tick = () => {
            const t = (Date.now() - start) / duration;

            if (done) {
                return;
            }

            if (t > 1) {
                dispatch({ type: 'TICK', payload: 1 });
                dispatch({ type: 'DONE' });
                return;
            }
            dispatch({ type: 'TICK', payload: t });
            requestAnimationFrame(tick);
        }

        tick();
        return () => {
            done = true;
        }
    }, [animation.animating, animation.direction, duration]);

    const open = useCallback(
        () => {
            dispatch({ type: 'OPEN' })
        },
        [dispatch],
    );

    const close = useCallback(
        () => {
            dispatch({ type: 'CLOSE' })
        },
        [dispatch],
    );

    return { animation, open, close }
}

export const FiltersSidebar: FunctionComponent = () => {

    const { animation, open, close } = useAnimation(350);

    const sidebarContext = useContext(SidebarContext);
    const t = useLabels();
    const closeButtonRef = useRef<HTMLButtonElement>(null);

    useEffect(() => {
        if (sidebarContext.isOpen) {
            requestAnimationFrame(() => {
                closeButtonRef.current?.focus({ preventScroll: true });
            })
        }
    }, [sidebarContext.isOpen, closeButtonRef])


    useEffect(() => {
        if (sidebarContext.isOpen) {
            open();
        } else {
            close();
        }
    }, [sidebarContext.isOpen, open, close]);


    if (!(sidebarContext.isOpen || animation.animating)) {
        return null;
    }

    return <>
        <BackgroundEle
            style={{ ...animation.styles.bg }}
            onClick={sidebarContext.close}
        />
        <SidebarEle style={{
            ...animation.styles.sidebar
        }}>
            <SidebarTitle>
                {/* @ts-expect-error */}
                <Typography mb={0} fontSize="20px" lineHeight="1.25" fontWeight="700" as="h2">{t('titles.filters')}</Typography>
                <CloseButton type="button" ref={closeButtonRef} onClick={close} aria-label="Close">
                    <Icon type="close" />
                </CloseButton>
            </SidebarTitle>
            <SidebarContent>
                <TaxonomySelector />
            </SidebarContent>
        </SidebarEle >
    </>
}
